<template lang="html">
  <div class="">

    <!-- 头部 -->
    <default-head header="添加收货地址"></default-head>
    <div class="address-page">

      <!-- 表单 -->
      <form action="#">
        <div class="address-page__list">
          <form-line label="收货人:" placeholder="请输入姓名"></form-line>
          <form-line label="联系电话:" placeholder="请输入手机号码"></form-line>
          <city-select></city-select>
          <address-input></address-input>
        </div>

        <!-- 提交按钮 -->
        <sub-button></sub-button>

        <!-- 取消按钮 -->
        <sub-button text="取消" bgcolor="#c3c3c3" marginTop=".4rem"></sub-button>

      </form>

    </div>
  </div>
</template>

<script>
import defaultHead from '@/components/head/defaultHead'
import subButton from '@/components/common/subButton'
import formLine from '@/components/common/formLine'
import citySelect from '@/components/common/citySelect'
import addressInput from '@/components/common/addressInput'
// import infoLine from '@/components/common/infoLine'

export default {
  components: { defaultHead, subButton, citySelect, addressInput, formLine }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';

.address-page {
  margin-top: .88rem;
  .address-page__list {
    padding-left: .22rem;
    background: @whiteColor;
    margin-bottom: .6rem;
  }
  
}

</style>



